<template>
  <div class="full-pop">
    <slot></slot>
    
    <img
      :src="statusImg"
      class="reserve-status-img"
    >

    <p class="font-primary">
      {{statusTitle}}
    </p>

    <p v-html="statusText"></p>
  </div>
</template>

<script>
export default {
  props: {
    result: {
      type: String,
      required: true,
    },
  },
  computed: {
    statusImg() {
      return require(`@img/icon/reserve-${this.result}.svg`);
    },
    statusTitle() {
      return this.result === 'success' ? '' : '';
    },
    statusText() {
      let msg = '';
      switch (this.result) {
        case 'success':
          msg = '<br>';
          break;
        case 'fail':
          msg = '<br>';
          break;
        default:
          break;
      }
      return msg;
    },
  },
}
</script>
